:root {
  --text-color: rgb(238, 237, 229);
}

.join {
  font-weight: 400;
  font-size: 25px;
}

.unsubscribe {
  font-weight: 400;
  font-size: 20px;
}

.footer-container {
  padding-top: 80px;
  background-color: black;
  color: var(--text-color);
  font-family: "Open Sans", sans-serif;
  font-weight: 100;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.email-input {
  padding: 12px;
  padding-left: 15px;
  width: 250px;
  border-radius: 4px;
  outline: none;
}

.input-button {
  padding: 12px 18px;
  background-color: transparent;
  border: var(--text-color) 1px solid;
  border-radius: 3px;
  width: 120px;
  color: var(--text-color);
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 0.5px;
  margin: 10px;
  cursor: pointer;
  transition: linear 0.1s;
}
.input-button:hover {
  color: black;
  background-color: rgb(237, 237, 237);
}

.footer-input-container {
  position: relative;
  bottom: 10px;
  margin-top: 50px;
}

.second-segment {
  margin-top: 0px;
  display: flex;
  flex-direction: row;
  margin-bottom: 80px;
  justify-content: center;
  width: 80vw;
  border: transparent 2px solid;
  
}
.column {
  position: relative;
  margin: 0 70px;
  border: transparent 2px solid;
  text-align: left;
}

.first-segment {
  border: transparent 5px solid;
  padding: 5px;
}

.links1,
.links2 {
  display: flex;
}

.link-header {
  border: transparent 2px solid;
  height: 88px;
  width: 120%;
  margin-bottom: 5px;
}

.column h4 {
  margin: 0;
  margin: 9% 0;
  padding: 0;
  display: flex;
  align-items: flex-start;
  text-align: left;
  font-size: 90%;
  cursor: pointer;
}

.column h3 {
  position: relative;
  top: 5px;
}

@media only screen and (max-width: 800px) {
  .links1,
  .links2 {
    width: 90vw;
  }

  .second-segment {
    flex-wrap: wrap;
  }
}
