.navbar {
  position: absolute;
  display: flex;
  justify-content: space-between;
  background-color: black;
  z-index: 999;
  width: 100vw;;

    top: 0;

}

.links {
  display: flex;
justify-content: center;
align-items: center;
  
 
 padding: 10px;
  font-size: 126%;
  padding-bottom: 5px;
  margin-right:8%;
  border-top: rgb(153, 150, 150) 1px solid;
  transition: all ease 
}
.links a {
  text-decoration: none;
  color: white;
  padding: 5px 22px 8px 22px;
  font-weight: 700;

}




.navlink:hover {

  color: rgb(191, 151, 151);
 
  
  

}


.navbar h1{
  color: white;
  
}

.navbar h1 i{
 padding-left: 0px;
}


.signup{
 border: white 1px solid;
 border-radius: 3px;
 text-transform: uppercase;
 transition: ease .17s
 
}



.logo {
  padding-left: 120px;
}

.logo i {
  font-size: 30px;
}


.icon{

position: absolute;
color: white;
right: 5%;
font-size: 30px;
visibility: hidden;
cursor: pointer;
}

.signup:hover{
  background-color: white;
color:  black;text-emphasis-color: black;
}


@media only screen and (max-width: 667px) {

  .navbar {

 
  display: flex;
 flex-direction: column;
background-color: transparent;
 width: 100%;
 justify-content: center;

 
  }

.links {
  display: flex;
justify-content:start;
align-items: center;
  flex-direction: column;
  float: right;
 
 padding: 0px;
  font-size: 126%;
  padding-bottom: 5px;
  margin-right:0%;

  height: 100vh;
 background-color:rgba(22, 19, 19, 0.95);
}

.links a {
  padding: 10px 15px;
  margin: 30px;
  font-size: 25px;
  
 
}



.logo {
  position: relative;
  padding-left: 0px;
 width: inherit;
background-color: black;
 display: flex;
 flex-direction: row;
 align-items: center;
 justify-content: center;;


}

.logo h1{
  width: inherit;
  position: relative;

  justify-self: center;
  font-size: 30px;


  
}

.navbar h1{
  font-size: 40px;
}


.icon{

position: absolute;
color: white;
top: 15%;
visibility: visible;
}

.nav-hidden{
visibility: hidden;
display: none;
transition: all ease 2s;
}
.nav-show{
  
}


}