* {
  box-sizing: border-box;
}

body{
    background-color: white;
}

.cards__item__pic-wrap img {
  height:  100%;width: 100%;
  border-radius: 15px 15px 0 0;
  transition: all ease .4s;
  /* border: rgb(114, 255, 185) solid 5px; */
}
.cards__item__pic-wrap:hover img {
 transform: scale(1.038)

  /* border: rgb(114, 255, 185) solid 5px; */
}





li {
  list-style: none;
}

.cards__item__pic-wrap {
  /* border: rgb(220, 28, 245) solid 5px; */
 height: 23vw;
  padding: 0;
  display: block;
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  top: 0;
  align-self: start;
     overflow: hidden;
     position: relative;

     border-radius: 18px  18px 0px 0px ;
     z-index: 1;
}

.label{
    height: 30px;
    width: auto;
    background-color: rgb(43, 162, 216);
    position: absolute;
    bottom: 0;
    z-index: 10;
    left: 15px;
    padding: 0 15px ;
    text-align: center;
    border-radius: 5px 5px 0 0;
    color: white;
    font-weight: 600;
}


 .bottom-images .label{
   padding-top: 3px;
 }

figure {
  /* border: rgb(0, 0, 0) solid 1px; */
  display: block;

}

