}



.bottom-images > div .cards__item__pic-wrap
{
    height: 300px;;
    
}


 .cards__item__pic-wrap{
  cursor: pointer;
}


.bottom-images .cards__item__text {

  margin-left:5px;
  position: absolute;
  right: 0%;
  width: 100%;
  top: -1%;
}




.cards__item__link {
  /* border: rgb(255, 255, 255) solid 5px; */
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;

}

