
html,
body
{
  height:100%;
  margin: 0;
}



.header {
    position: relative;
  

  background-color: rgb(53, 31, 31);
  width: 100%;
  height: 100vh;

 
  display: flex;
  justify-content: center;
  align-items: center;

  overflow: hidden;

  
}

.title{
   color: white;
   position: relative;
   bottom: 3px;
}

.adventure{
  font-size: 5vw;
  margin-bottom: 30px;
}

.waiting{
  font-size: 4vw;
  margin-top: 50px;
  position: relative;
  bottom: 50px;
  
}

.buttonContainer{
  position: relative;
  bottom: 70px;
}

.headerButton{
  padding: 15px 25px;
  background-color: transparent;
  border: white 2px solid; 
  border-radius: 3px;
  color: white; 
  font-size: 20px;
  margin: 20px;
  transition: all ease-in-out .2s;
}

#trailor{
  color: black;
  background: white;
}

#trailor:hover{
  color: rgb(192, 64, 64);
  background: white;
 
  
  
}

#play{
  margin-left: 10px;
}

.headerButton:hover{
  background-color: white;
  color: black;
}
.header-image{
  position: absolute;
  top: 0;
  left: 0;
}

@media only screen and (max-width: 667px) {

.adventure{
  font-size: 10vw;
}




.waiting{
position: relative;
top: -40px;
  margin-top: 50px;
  font-size: 200%;
  position: relative;
  bottom: 50px;
  
}

.buttonContainer{
position: relative;
top: -30px;
}


.headerButton{
  padding: 15px 25px;
  background-color: transparent;
  border: white 2px solid; 
  border-radius: 3px;
  color: white; 
  font-size: 17px;
  margin: 20px;
position: relative;
  transition: all ease-in-out .2s;
}

#trailor{
  color: black;
  background: white;
}


}